import { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Button } from 'reactstrap'
import { checkAvailabilty } from '../actions/userAction'
import BookingScreen from './BookingScreen'
const SearchList = (props) => {
  const usersearchBus = useSelector((store) => store.usersearchBus)
  const checkSeat = useSelector((store) => store.checkSeat)
  const { loading, response, error } = usersearchBus
  //  const { loading, response, error } = checkSeat

  function Booking(id, price) {
    sessionStorage.setItem('busId', id)
    sessionStorage.setItem('fare', price)
    props.history.push('/booking')
  }
  const dispatch = useDispatch()
  function Availabilty(busId) {
    const bookDate = sessionStorage.getItem('bookDate')
    dispatch(checkAvailabilty(busId, bookDate))
    sessionStorage.removeItem('bookDate')
  }

  useEffect(() => {}, [error, response, loading])
  useEffect(() => {}, [error, response, loading])
  return (
    <div>
      <table class="table table-dark table-striped">
        <thead>
          <tr>
            <th scope="col">BusNo</th>
            <th scope="col">Capacity</th>
            <th scope="col">BusType</th>
            <th scope="col">ArrivalTime</th>
            <th scope="col">DestinationTime</th>
            <th scope="col">Price</th>
            <th scope="col">Seta Availabilty</th>
            <th scope="col">Booking</th>
          </tr>
        </thead>
        <tbody>
          {response &&
            response &&
            response.length > 0 &&
            response.map((usersearchBus) => {
              return (
                <tr>
                  <td>{usersearchBus.busNo}</td>
                  <td>{usersearchBus.capacity}</td>
                  <td>{usersearchBus.type}</td>
                  <td>{usersearchBus.arrivalDate}</td>
                  <td>{usersearchBus.destinationDate}</td>
                  <td>{usersearchBus.price}</td>
                  <td>
                    {' '}
                    <Button
                      color="danger"
                      onClick={() => Availabilty(usersearchBus.id)}>
                      Availabilty
                    </Button>
                    {/* <td>{response.checkSeat.availableSeats}</td> */}
                  </td>
                  <td>
                    {' '}
                    <Button
                      color="danger"
                      onClick={() =>
                        Booking(usersearchBus.id, usersearchBus.price)
                      }>
                      Booking
                    </Button>
                  </td>
                </tr>
              )
            })}
        </tbody>
      </table>
    </div>
  )
}

export default SearchList
