import './Registration.css'
import { Link } from 'react-router-dom'
import Navigation from '../components/Navigation'
import Header from '../components/Header'
import { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { signup } from '../actions/userAction'
import { addBus } from '../actions/adminAction'

const AddBus = (props) => {
  const [busNo, setBusNo] = useState('')
  const [capacity, setCapacity] = useState('')
  const [arrivalDate, setArrivalDate] = useState('')
  const [destinationDate, setDestinationDate] = useState('')
  const [price, setPrice] = useState('')

  const adminAddBus = useSelector((store) => store.adminAddBus)
  const { loading, error, response, isSuccess } = adminAddBus

  const dispatch = useDispatch()

  const register = () => {
    const routeId = sessionStorage.getItem('routesId')
    dispatch(
      addBus(busNo, capacity, arrivalDate, destinationDate, price, routeId)
    )
    sessionStorage.removeItem('routesId')
    props.history.push('/homeadmin')
  }

  useEffect(() => {
    console.log('use effect called: ')
    console.log('loading: ', loading)
    console.log('response: ', response)
    console.log('error: ', error)

    if (isSuccess) {
      // props.history.push('/homeadmin')
    } else if (error) {
      console.log(error)
      alert('error while making API call')
    }
  }, [loading, response, error, isSuccess])

  return (
    <div>
      <div class="container">
        <div class="row">
          <div class="col-md-3 col-xs-12"> </div>
          <div class="col-md-6 col-xs-12">
            <h1 class="class_center">AddBus</h1>
            <div class="mb-3">
              <label for="inputtext" class="form-label">
                BusNo
              </label>
              <input
                type="number"
                class="form-control"
                id="BusNo"
                placeholder="BusNo"
                onChange={(e) => {
                  setBusNo(e.target.value)
                }}
              />
            </div>
            <div class="mb-3">
              <label for="inputtext" class="form-label">
                Capacity
              </label>
              <input
                type="number"
                class="form-control"
                id="capacity"
                placeholder="Capcity"
                onChange={(e) => {
                  setCapacity(e.target.value)
                }}
              />
            </div>
            <div class="mb-3">
              <label for="inputtext" class="form-label">
                ArrivalTime
              </label>
              <input
                type="time"
                class="form-control"
                placeholder="ArrivalTime"
                onChange={(e) => {
                  setArrivalDate(e.target.value)
                }}
              />
            </div>
            <div class="mb-3">
              <label for="inputtext" class="form-label">
                DestinationTime
              </label>
              <input
                type="time"
                class="form-control"
                placeholder="DestinationTime"
                onChange={(e) => {
                  setDestinationDate(e.target.value)
                }}
              />
            </div>
            <div class="mb-3">
              <label for="inputtext" class="form-label">
                Price
              </label>
              <input
                type="number"
                class="form-control"
                placeholder="Price"
                onChange={(e) => {
                  setPrice(e.target.value)
                }}
              />
            </div>
            <div class="row">
              <button type="button" class="btn btn-success" onClick={register}>
                AddBus
              </button>
            </div>
          </div>
          <div class="col-md-3 col-xs-12"></div>
        </div>
      </div>
    </div>
  )
}

export default AddBus
