import './UpdateProfile.css'
import { UserUpdateProfile } from '../actions/userAction'
import { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'

const UpdateProfileUsers = (props) => {
  const [email, setEmail] = useState('')
  const [city, setCity] = useState('')
  const [pin, setPin] = useState('')
  const [age, setAge] = useState('')
  const [contactNo, setContactNo] = useState('')

  const dispatch = useDispatch()

  const userupdateprofile = useSelector((store) => store.userupdateprofile)

  const { loading, response, error, isSuccess } = userupdateprofile
  useEffect(() => {
    console.log('use effect called: ')
    console.log('loading: ', loading)
    console.log('response: ', response)
    console.log('error: ', error)

    if (isSuccess) {
      // user successfully got registered
      // props.history.push('/homeadmin')
    } else if (error) {
      // there is an error while making the API call
      console.log(error)
      alert('error while making API call')
    }
  }, [loading, response, error, isSuccess])

  const onUpdateProfile = () => {
    const id = sessionStorage.getItem('token')
    dispatch(UserUpdateProfile(email, city, pin, age, contactNo, id))
    props.history.push('/home')
  }

  return (
    <div>
      <div class="container">
        <div class="row">
          <div class="col-md-3 col-xs-12"> </div>
          <div class="col-md-6 col-xs-12">
            <h1 class="class_center">Update Profile</h1>
            <div class="mb-3">
              <label for="inputtext" class="form-label">
                Email
              </label>
              <input
                type="email"
                class="form-control"
                id="email"
                placeholder="Eamil"
                onChange={(e) => {
                  setEmail(e.target.value)
                }}
              />
            </div>
            <div class="mb-3">
              <label for="inputtext" class="form-label">
                City
              </label>
              <input
                type="text"
                class="form-control"
                placeholder="City"
                onChange={(e) => {
                  setCity(e.target.value)
                }}
              />
            </div>
            <div class="mb-3">
              <label for="inputtext" class="form-label">
                Pin
              </label>
              <input
                type="text"
                class="form-control"
                placeholder="Pin"
                onChange={(e) => {
                  setPin(e.target.value)
                }}
              />
            </div>
            <div class="mb-3">
              <label for="inputtext" class="form-label">
                Age
              </label>
              <input
                type="text"
                class="form-control"
                placeholder="Age"
                onChange={(e) => {
                  setAge(e.target.value)
                }}
              />
            </div>
            <div class="mb-3">
              <label
                for="inputtext"
                class="form-label"
                onChange={(e) => {
                  setContactNo(e.target.value)
                }}>
                ContactNo
              </label>
              <input
                type="text"
                class="form-control"
                placeholder="Contact Number"
              />
            </div>
            <div class="row">
              <button
                type="button"
                class="btn btn-success"
                onClick={onUpdateProfile}>
                Submit
              </button>
            </div>
          </div>
          <div class="col-md-3 col-xs-12"></div>
        </div>
      </div>
    </div>
  )
}

export default UpdateProfileUsers
