import {
  USER_BOOKTICKET_FAIL,
  USER_BOOKTICKET_REQUEST,
  USER_BOOKTICKET_SUCCESS,
  USER_CANCLETICKET_FAIL,
  USER_CANCLETICKET_REQUEST,
  USER_CANCLETICKET_SUCCESS,
  USER_CHANGEPASSWORD_FAIL,
  USER_CHANGEPASSWORD_REQUEST,
  USER_CHANGEPASSWORD_SUCCESS,
  USER_CHECKSEAT_FAIL,
  USER_CHECKSEAT_REQUEST,
  USER_CHECKSEAT_SUCCESS,
  USER_SEARCHBUS_FAIL,
  USER_SEARCHBUS_REQUEST,
  USER_SEARCHBUS_SUCCESS,
  USER_SIGNIN_FAIL,
  USER_SIGNIN_REQUEST,
  USER_SIGNIN_SUCCESS,
  USER_SIGNOUT,
  USER_SIGNUP_FAIL,
  USER_SIGNUP_REQUEST,
  USER_SIGNUP_SUCCESS,
  USER_TICKETLIST_FAIL,
  USER_TICKETLIST_REQUEST,
  USER_TICKETLIST_SUCCESS,
  USER_UPDATEPROFILE_FAIL,
  USER_UPDATEPROFILE_REQUEST,
  USER_UPDATEPROFILE_SUCCESS,
} from '../constants/userConstants.js'

export const userSignupReducer = (state = {}, action) => {
  switch (action.type) {
    case USER_SIGNUP_REQUEST:
      return { loading: true }
    case USER_SIGNUP_SUCCESS:
      return { loading: false, response: action.payload, isSuccess: true }
    case USER_SIGNUP_FAIL:
      return { loading: false, error: action.payload }
    default:
      return state
  }
}

export const userSigninReducer = (state = {}, action) => {
  switch (action.type) {
    case USER_SIGNIN_REQUEST:
      return { loading: true }
    case USER_SIGNIN_SUCCESS:
      return {
        loading: false,
        response: action.payload,
        role: action.payload.role,
        userId: action.payload.userId,
      }
    case USER_SIGNIN_FAIL:
      return { loading: false, error: action.payload }
    case USER_SIGNOUT:
      return {}
    default:
      return state
  }
}

