import './Registration.css'
import { Link } from 'react-router-dom'
import Navigation from '../components/Navigation'
import Header from '../components/Header'
import { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { signup } from '../actions/userAction'
const RegistrationScreen = (props) => {
  const [name, setName] = useState('')
  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')
  const [contactNo, setContactNo] = useState('')
  const [city, setCity] = useState('')
  const [pin, setPin] = useState('')
  const [age, setAge] = useState('')
  const [sex, setSex] = useState('')

  const userSignup = useSelector((store) => store.userSignup)
  const { loading, error, response, isSuccess } = userSignup

  const dispatch = useDispatch()

  const register = () => {
    dispatch(signup(name, email, password, contactNo, city, pin, sex, age))
  }

  useEffect(() => {
    console.log('use effect called: ')
    console.log('loading: ', loading)
    console.log('response: ', response)
    console.log('error: ', error)

    if (isSuccess) {
      props.history.push('/')
    } else if (error) {
      console.log(error)
      alert('error while making API call')
    }
  }, [loading, response, error, isSuccess])

  return (
    <div>
      <div class="container">
        <div class="row">
          <div class="col-md-3 col-xs-12"> </div>
          <div class="col-md-6 col-xs-12">
            <h1 class="class_center">SignUp</h1>
            <div class="mb-3">
              <label for="inputtext" class="form-label">
                Name
              </label>
              <input
                type="text"
                class="form-control"
                id="FirstName"
                placeholder="Name"
                onChange={(e) => {
                  setName(e.target.value)
                }}
              />
            </div>
            <div class="mb-3">
              <label for="inputtext" class="form-label">
                Email
              </label>
              <input
                type="email"
                class="form-control"
                id="email"
                placeholder="Eamil"
                onChange={(e) => {
                  setEmail(e.target.value)
                }}
              />
            </div>
            <div class="mb-3">
              <label class="form-label">Gender</label>
              <input
                type="radio"
                id="genedrM"
                name="gender"
                value="MALE"
                onChange={(e) => {
                  setSex(e.target.value)
                }}
              />
              Male
              <input
                type="radio"
                id="genedrF"
                name="gender"
                value="FEMALE"
                onChange={(e) => {
                  setSex(e.target.value)
                }}
              />
              Female
            </div>
            <div class="mb-3">
              <label for="inputtext" class="form-label">
                Password
              </label>
              <input
                type="password"
                class="form-control"
                id="password"
                placeholder="Password"
                onChange={(e) => {
                  setPassword(e.target.value)
                }}
              />
              <small>
                Password must have digit,special symbol and charcter
              </small>
            </div>
            <div class="mb-3">
              <label for="inputtext" class="form-label">
                City
              </label>
              <input
                type="text"
                class="form-control"
                placeholder="City"
                onChange={(e) => {
                  setCity(e.target.value)
                }}
              />
            </div>
            <div class="mb-3">
              <label for="inputtext" class="form-label">
                Pin
              </label>
              <input
                type="number"
                class="form-control"
                placeholder="Pin"
                onChange={(e) => {
                  setPin(e.target.value)
                }}
              />
            </div>
            <div class="mb-3">
              <label for="inputtext" class="form-label">
                Age
              </label>
              <input
                type="number"
                class="form-control"
                placeholder="Age"
                onChange={(e) => {
                  setAge(e.target.value)
                }}
              />
            </div>
            <div class="mb-3">
              <label for="inputtext" class="form-label">
                ContactNo
              </label>
              <input
                type="text"
                class="form-control"
                placeholder="contactNo"
                onChange={(e) => {
                  setContactNo(e.target.value)
                }}
              />
            </div>
            <div class="row">
              <button type="button" class="btn btn-success" onClick={register}>
                Submit
              </button>
            </div>
            <div class="color">
              <b>
                {' '}
                If account?&nbsp;
                <Link to="/signIn">
                  <a>
                    <u>SignIn hear</u>
                  </a>
                </Link>
              </b>
            </div>
          </div>
          <div class="col-md-3 col-xs-12"></div>
        </div>
      </div>
    </div>
  )
}

export default RegistrationScreen
