import { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Button } from 'reactstrap'
import { cancleTicket, userTicketsById } from '../actions/userAction'
import { toast } from 'react-toastify'
import base_url from '../api/api'
import axios from 'axios'
import { addseat, removeseat } from '../actions/adminAction'
const MyBookingScreen = (props) => {
  const dispatch = useDispatch()
  const userTicketList = useSelector((store) => store.userTicketList)
  const { error, response, loading } = userTicketList
  useEffect(() => {
    const userId = sessionStorage.getItem('token')
    dispatch(userTicketsById(userId))
  }, [])
  useEffect(() => {}, [error, response, loading])

  function oncancel(id, busId, bookDate, noOfSeats) {
    dispatch(cancleTicket(id))
    dispatch(addseat(busId, bookDate, noOfSeats))
    dispatch(removeseat(busId, bookDate))
    props.history.push('/home')
  }

  return (
    <div>
      <table class="table table-dark table-striped">
        <thead>
          <tr>
            <th scope="col">TicketId</th>
            <th scope="col">BusId</th>
            <th scope="col">Owner Name</th>
            <th scope="col">No of Seats</th>
            <th scope="col">Fare</th>
            <th scope="col">Book Date</th>
            <th scope="col">Cancel Booking</th>
          </tr>
        </thead>
        <tbody>
          {response &&
            response &&
            response.length > 0 &&
            response.map((userTicketList) => {
              return (
                <tr>
                  <td>{userTicketList.id}</td>
                  <td>{userTicketList.busId.id}</td>
                  <td>{userTicketList.ownerName}</td>
                  <td>{userTicketList.noOfSeats}</td>
                  <td>{userTicketList.fare}</td>
                  <td>{userTicketList.bookDate}</td>
                  <td>
                    <Button
                      color="danger"
                      onClick={() =>
                        oncancel(
                          userTicketList.id,
                          userTicketList.busId.id,
                          userTicketList.bookDate,
                          userTicketList.noOfSeats
                        )
                      }>
                      Cancle Ticket
                    </Button>
                  </td>
                </tr>
              )
            })}
        </tbody>
      </table>
    </div>
  )
}

export default MyBookingScreen
