import { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { searchBus } from '../actions/userAction'
import './home.css'
import Home from '../sliderTest'
import Header from '../components/Header'

const HomeScreen = (props) => {
  const [source, setSource] = useState('')
  const [destination, setDestination] = useState('')
  const [date, setDate] = useState('')

  const usersearchBus = useSelector((store) => store.usersearchBus)
  const { loading, response, error, isSuccess } = usersearchBus

  const dispatch = useDispatch()

  function getSubmit() {
    sessionStorage.setItem('bookDate', date)
    dispatch(searchBus(source, destination, date))
    props.history.push('/searchList')
  }

  useEffect(() => {
    if (isSuccess) {
      // props.history.push('/home')
    } else if (error) {
      console.log(error)
      alert('error while making API call')
    }
  }, [loading, response, error, isSuccess])

  return (
    <div>
      <Home />

      <Header title="Search Bus Here" />
      <div class="textbox">
        <i class="fa fa-user" aria-hidden="true"></i>
        <input
          type="text"
          placeholder="Source"
          onChange={(e) => {
            setSource(e.target.value)
          }}
        />
      </div>
      <div class="textbox">
        <i class="fa fa-lock" aria-hidden="true"></i>
        <input
          type="text"
          placeholder="Destination"
          onChange={(e) => {
            setDestination(e.target.value)
          }}
        />
      </div>
      <div class="textbox">
        <i class="fa fa-lock" aria-hidden="true"></i>
        <input
          type="date"
          onChange={(e) => {
            setDate(e.target.value)
          }}
        />
      </div>
      <div class="row">
        <div class="btn btn-success" onClick={() => getSubmit()}>
          Submit
        </div>
      </div>
      <div></div>
    </div>
  )
}

export default HomeScreen
