import { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Link } from 'react-router-dom'
import { changepassword } from '../actions/userAction'
import Header from '../components/Header'
import './Login.css'

const ChangePassword = (props) => {
  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')

  const userChange = useSelector((store) => store.userChange)
  const { loading, response, error, isSuccess } = userChange

  const dispatch = useDispatch()

  const changep = () => {
    dispatch(changepassword(email, password))
  }

  useEffect(() => {
    if (isSuccess) {
      props.history.push('/')
    } else if (error) {
      console.log(error)
      alert('error while making API call')
    }
  }, [loading, response, error, isSuccess])

  return (
    <div>
      <div class="container">
        <div class="row">
          <div class="col-md-4">
            <div class="logo">
              <h3>Change Password</h3>
            </div>
            <div class="textbox">
              <i class="fa fa-user" aria-hidden="true"></i>
              <input
                type="text"
                placeholder="OldEmail"
                id="email"
                onChange={(e) => {
                  setEmail(e.target.value)
                }}
              />
            </div>
            <div class="textbox">
              <i class="fa fa-lock" aria-hidden="true"></i>
              <input
                type="password"
                placeholder="NewPassword"
                id="password"
                onChange={(e) => {
                  setPassword(e.target.value)
                }}
              />
            </div>
            <div class="row">
              <div class="btn btn-success" onClick={changep}>
                Submit
              </div>
            </div>
            <div class="signup">
              <b>
                No account?&nbsp;
                <Link to="/register">
                  <u>Create new account</u>
                </Link>
              </b>
            </div>
            <div class="signup">
              <Link to="/signIn">
                <b>
                  <u>Back To Login Page</u>
                </b>
              </Link>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default ChangePassword
