import {
  ADMIN_ADDROUTE_REQUEST,
  ADMIN_ADDROUTE_SUCCESS,
  ADMIN_ROUTE_FAIL,
  BUSLIST_FETCH_FAIL,
  BUSLIST_FETCH_REQUEST,
  BUSLIST_FETCH_SUCCESS,
  ROUTESLIST_FETCH_REQUEST,
  ROUTESLIST_FETCH_FAIL,
  ROUTESLIST_FETCH_RESET,
  ROUTESLIST_FETCH_SUCCESS,
  BUSADD_FETCH_REQUEST,
  BUSADD_FETCH_SUCCESS,
  BUSADD_FETCH_FAIL,
  USERLISTBYBUS_FETCH_REQUEST,
  USERLISTBYBUS_FETCH_SUCCESS,
  USERLISTBYBUS_FETCH_FAIL,
  ADMIN_REMOVESEAT_REQUEST,
  ADMIN_REMOVESEAT_SUCCESS,
  ADMIN_ADDSEAT_REQUEST,
  ADMIN_REMOVESEAT_FAIL,
  ADMIN_ADDSEAT_SUCCESS,
  ADMIN_ADDSEAT_FAIL,
} from '../constants/adminConstants'
import axios from 'axios'

export const addroutes = (source, destination) => {
  return (dispatch) => {
    dispatch({
      type: ADMIN_ADDROUTE_REQUEST,
    })
    const header = {
      headers: {
        'Content-Type': 'application/json',
      },
    }
    const body = {
      source,
      destination,
    }
    const url = '/admin/routes'
    axios
      .post(url, body, header)
      .then((response) => {
        dispatch({
          type: ADMIN_ADDROUTE_SUCCESS,
          payload: response.data,
        })
      })
      .catch((error) => {
        dispatch({
          type: ADMIN_ROUTE_FAIL,
          payload: error,
        })
      })
  }
}

export const getBuses = () => {
  return (dispatch) => {
    dispatch({
      type: BUSLIST_FETCH_REQUEST,
    })

    const header = {
      headers: {
        'Content-Type': 'application/json',
        token: sessionStorage['token'],
      },
    }

    const url = '/admin/bus'
    axios
      .get(url, header)
      .then((response) => {
        dispatch({
          type: BUSLIST_FETCH_SUCCESS,
          payload: response.data,
        })
      })
      .catch((error) => {
        dispatch({
          type: BUSLIST_FETCH_FAIL,
          payload: error,
        })
      })
  }
}

