export const addBus = (
  busNo,
  capacity,
  arrivalDate,
  destinationDate,
  price,
  routeId
) => {
  return (dispatch) => {
    dispatch({
      type: BUSADD_FETCH_REQUEST,
    })
    const header = {
      headers: {
        'Content-Type': 'application/json',
      },
    }
    const body = {
      busNo,
      capacity,
      arrivalDate,
      destinationDate,
      price,
    }
    const url = '/admin/bus/' + routeId
    axios
      .post(url, body, header)
      .then((response) => {
        dispatch({
          type: BUSADD_FETCH_SUCCESS,
          payload: response.data,
        })
      })
      .catch((error) => {
        dispatch({
          type: BUSADD_FETCH_FAIL,
          payload: error,
        })
      })
  }
}

