import './Registration.css'
import { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { BookTickets } from '../actions/userAction'
const BookingScreen = (props) => {
  const [ownerName, setOwnerName] = useState('')
  const [phoneNo, setPhoneNo] = useState('')
  let [fare, setFare] = useState('')
  const [noOfSeats, setNoOfSeats] = useState('')
  const [bookDate, setDate] = useState('')
  const bookticket = useSelector((store) => store.bookticket)
  const { loading, response, error, isSuccess } = bookticket

  const dispatch = useDispatch()
  const price = sessionStorage.getItem('fare')
  const booking = () => {
    const busId = sessionStorage.getItem('busId')
    const userId = sessionStorage.getItem('token')
    fare = document.getElementById('totalfare').value
    dispatch(
      BookTickets(busId, userId, ownerName, phoneNo, fare, noOfSeats, bookDate)
    )
    props.history.push('/home')
    sessionStorage.removeItem('busId')
    sessionStorage.removeItem('fare')
  }

  useEffect(() => {
    console.log('use effect called: ')
    console.log('loading: ', loading)
    console.log('response: ', response)
    console.log('error: ', error)

    if (isSuccess) {
      // alert("ticket booking done")
      // props.history.push('/home')
    } else if (error) {
      console.log(error)
      alert('error while making API call')
    }
  }, [loading, response, error, isSuccess])

  return (
    <div>
      <div class="container">
        <div class="row">
          <div class="col-md-3 col-xs-12"> </div>
          <div class="col-md-6 col-xs-12">
            <h1 class="class_center">Booking Form</h1>
            <div class="mb-3">
              <label for="inputtext" class="form-label">
                OwnerName
              </label>
              <input
                type="text"
                class="form-control"
                placeholder="OwnerName"
                onChange={(e) => {
                  setOwnerName(e.target.value)
                }}
              />
            </div>
            <div class="mb-3">
              <label for="inputtext" class="form-label">
                OwnerPhone
              </label>
              <input
                type="text"
                class="form-control"
                placeholder="OwnerPhone"
                onChange={(e) => {
                  setPhoneNo(e.target.value)
                }}
              />
            </div>

            <div class="mb-3">
              <label for="inputtext" class="form-label">
                Choose Date
              </label>
              <input
                type="date"
                class="form-control"
                placeholder="GivesTodaysDate"
                onChange={(e) => {
                  setDate(e.target.value)
                }}
              />
            </div>
            <div class="mb-3">
              <label for="inputtext" class="form-label">
                NoOfSeat
              </label>
              <input
                type="number"
                class="form-control"
                placeholder="No Of seates"
                onChange={(e) => {
                  setNoOfSeats(e.target.value)
                }}
              />
            </div>
            <div class="mb-3">
              <label for="inputtext" class="form-label">
                Total Fare
              </label>
              <input
                type="number"
                class="form-control"
                placeholder="Total Fare"
                readOnly
                id="totalfare"
                value={price * noOfSeats}
              />
            </div>
            <div class="row">
              <button type="button" class="btn btn-success" onClick={booking}>
                Submit
              </button>
            </div>
          </div>
          <div class="col-md-3 col-xs-12"></div>
        </div>
      </div>
    </div>
  )
}

export default BookingScreen
