import { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { getRoutesList } from '../actions/adminAction'
import { toast } from 'react-toastify'
import base_url from '../api/api'
import axios from 'axios'
import { Button } from 'reactstrap'

const RoutesList = (props) => {
  const dispatch = useDispatch()
  const adminRoutesList = useSelector((store) => store.adminRoutesList)
  const { error, response, loading } = adminRoutesList

  const deleteRoutes = (id) => {
    axios.delete(`${base_url}/admin/routes/${id}`).then(
      (response) => {
        console.log(response)
        toast.success('deleted')
        dispatch(getRoutesList())
      },
      (error) => {
        console.log(error)
        toast.error('Not deleted')
      }
    )
  }
  function BusAdd(id) {
    sessionStorage.setItem('routesId', id)
    props.history.push('/addbus')
  }

  useEffect(() => {
    dispatch(getRoutesList())
  }, [])
  useEffect(() => {}, [error, response, loading])
  return (
    <div>
      <table class="table table-dark table-striped">
        <thead>
          <tr>
            <th scope="col">Id</th>
            <th scope="col">Source</th>
            <th scope="col">Destination</th>
            <th scope="col">Delete</th>
            <th scope="col">AddBus</th>
          </tr>
        </thead>
        <tbody>
          {response &&
            response &&
            response.length > 0 &&
            response.map((adminRoutesList) => {
              return (
                <tr>
                  <td>{adminRoutesList.id}</td>
                  <td>{adminRoutesList.source}</td>
                  <td>{adminRoutesList.destination}</td>
                  <td>
                    <Button
                      color="danger"
                      onClick={() => {
                        deleteRoutes(adminRoutesList.id)
                      }}>
                      Delete
                    </Button>
                  </td>
                  <td>
                    <Button
                      onClick={() => BusAdd(adminRoutesList.id)}
                      color="danger">
                      AddBus
                    </Button>
                  </td>
                </tr>
              )
            })}
        </tbody>
      </table>
    </div>
  )
}

export default RoutesList
