import { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { BookingListByBus } from '../actions/adminAction'
import './home.css'

const BookingList = (props) => {
  const [busId, SetBusNo] = useState('')
  const [bookDate, setBookDate] = useState('')

  const bookingList = useSelector((store) => store.bookingList)
  const { loading, response, error, isSuccess } = bookingList

  const dispatch = useDispatch()

  const getSubmit = () => {
    dispatch(BookingListByBus(busId, bookDate))
  }

  useEffect(() => {
    if (isSuccess) {
    } else if (error) {
      console.log(error)
      alert('error while making API call')
    }
  }, [loading, response, error, isSuccess])

  return (
    <div>
      <div class="textbox">
        <i class="fa fa-user" aria-hidden="true"></i>
        <input
          type="text"
          placeholder="BusId"
          onChange={(e) => {
            SetBusNo(e.target.value)
          }}
        />
      </div>
      <div class="textbox">
        <i class="fa fa-lock" aria-hidden="true"></i>
        <input
          type="date"
          placeholder="Date"
          onChange={(e) => {
            setBookDate(e.target.value)
          }}
        />
      </div>
      <div class="row">
        <div class="btn btn-success" onClick={getSubmit}>
          Submit
        </div>
        <div>
          <table class="table table-dark table-striped">
            <thead>
              {response && (
                <tr>
                  <th scope="col">TicketId</th>
                  <th scope="col">Owner Name</th>
                  <th scope="col">Book Date</th>
                  <th scope="col">Contect Number</th>
                  <th scope="col">No of Seats</th>
                  <th scope="col">Bus Id</th>
                </tr>
              )}
            </thead>
            <tbody>
              {response &&
                response &&
                response.length > 0 &&
                response.map((bookingList) => {
                  return (
                    <tr>
                      <td>{bookingList.id}</td>
                      <td>{bookingList.ownerName}</td>
                      <td>{bookingList.bookDate}</td>
                      <td>{bookingList.phoneNo}</td>
                      <td>{bookingList.noOfSeats}</td>
                      <td>{bookingList.busId.id}</td>
                    </tr>
                  )
                })}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  )
}

export default BookingList
