import './AddRoutesScreen.css'
import Header from '../components/Header'
import { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { addroutes } from '../actions/adminAction'
import { toast } from 'react-toastify'

const AddRoutesScreen = (props) => {
  const [source, setSource] = useState('')
  const [destination, setDestination] = useState('')

  const adminAddRoutes = useSelector((store) => store.adminAddRoutes)
  const { loading, error, response, isSuccess } = adminAddRoutes

  const dispatch = useDispatch()

  const AddRoute = () => {
    dispatch(addroutes(source, destination))
    toast.success('Routes has been loaded')
    props.history.push('/homeadmin')
  }

  useEffect(() => {
    console.log('use effect called: ')
    console.log('loading: ', loading)
    console.log('response: ', response)
    console.log('error: ', error)

    if (isSuccess) {
      // props.history.push('/routesList')
    } else if (error) {
      console.log(error)
      alert('error while making API call')
    }
  }, [loading, response, error, isSuccess])

  return (
    <div>
      <Header title="Online Bus Booking System" />
      <div class="container">
        <div class="row">
          <div class="col-md-3 col-xs-12"> </div>
          <div class="col-md-6 col-xs-12">
            <h1 class="class_center">Add Routes</h1>
            <div class="mb-3">
              <label for="inputtext" class="form-label">
                Source
              </label>
              <input
                type="text"
                class="form-control"
                placeholder="source"
                onChange={(e) => {
                  setSource(e.target.value)
                }}
              />
            </div>
            <div class="mb-3">
              <label for="inputtext" class="form-label">
                Destination
              </label>
              <input
                type="text"
                class="form-control"
                placeholder="destination"
                onChange={(e) => {
                  setDestination(e.target.value)
                }}
              />
            </div>
            <div class="row">
              <button type="button" class="btn btn-success" onClick={AddRoute}>
                Submit
              </button>
            </div>
          </div>
          <div class="col-md-3 col-xs-12"></div>
        </div>
      </div>
    </div>
  )
}

export default AddRoutesScreen
