import { createStore, combineReducers, applyMiddleware } from 'redux'
import {
  userSignupReducer,
  userSigninReducer,
  userChangePasswordReducer,
  usersearchBusReducer,
  updateUserProfileReducer,
  userBookTicketReducer,
  userCheckSeatReducer,
  userTicketsListReducer,
  cancleTicketsReducer,
} from './reducers/userReducer'
import logger from 'redux-logger'
import thunk from 'redux-thunk'
import { composeWithDevTools } from 'redux-devtools-extension'
import {
  addSeatReducer,
  adminAddBusReducer,
  adminAddRoutesReducer,
  fetchBusesReducer,
  fetchRoutesReducer,
  removeSeatReducer,
  UserBookingListReducer,
} from './reducers/adminReducer'

// combined reducers
const reducers = combineReducers({
  userSignup: userSignupReducer,
  userSignin: userSigninReducer,
  userChange: userChangePasswordReducer,
  adminAddRoutes: adminAddRoutesReducer,
  adminBusList: fetchBusesReducer,
  adminRoutesList: fetchRoutesReducer,
  usersearchBus: usersearchBusReducer,
  userupdateprofile: updateUserProfileReducer,
  adminAddBus: adminAddBusReducer,
  bookticket: userBookTicketReducer,
  checkSeat: userCheckSeatReducer,
  bookingList: UserBookingListReducer,
  userTicketList: userTicketsListReducer,
  addseat: addSeatReducer,
  removeseat: removeSeatReducer,
  cancleticket: cancleTicketsReducer,
})

const store = createStore(
  reducers,
  composeWithDevTools(applyMiddleware(logger, thunk))
)

export default store
