import {
  USER_SIGNIN_FAIL,
  USER_SIGNIN_REQUEST,
  USER_SIGNIN_SUCCESS,
  USER_SIGNOUT,
  USER_SIGNUP_FAIL,
  USER_SIGNUP_REQUEST,
  USER_SIGNUP_SUCCESS,
  USER_CHANGEPASSWORD_REQUEST,
  USER_CHANGEPASSWORD_SUCCESS,
  USER_CHANGEPASSWORD_FAIL,
  USER_SEARCHBUS_REQUEST,
  USER_SEARCHBUS_FAIL,
  USER_SEARCHBUS_SUCCESS,
  USER_UPDATEPROFILE_SUCCESS,
  USER_UPDATEPROFILE_REQUEST,
  USER_UPDATEPROFILE_FAIL,
  USER_BOOKTICKET_REQUEST,
  USER_BOOKTICKET_SUCCESS,
  USER_BOOKTICKET_FAIL,
  USER_CHECKSEAT_REQUEST,
  USER_CHECKSEAT_SUCCESS,
  USER_CHECKSEAT_FAIL,
  USER_TICKETLIST_REQUEST,
  USER_TICKETLIST_FAIL,
  USER_TICKETLIST_SUCCESS,
  USER_CANCLETICKET_REQUEST,
  USER_CANCLETICKET_SUCCESS,
  USER_CANCLETICKET_FAIL,
} from '../constants/userConstants'
import axios from 'axios'

export const logout = () => {
  return (dispatch) => {
    sessionStorage.removeItem('token')
    dispatch({ type: USER_SIGNOUT })
    document.location.href = '/'
  }
}

export const signup = (
  name,
  email,
  password,
  contactNo,
  city,
  pin,
  sex,
  age
) => {
  return (dispatch) => {
    dispatch({
      type: USER_SIGNUP_REQUEST,
    })
    const header = {
      headers: {
        'Content-Type': 'application/json',
      },
    }
    const body = {
      name,
      email,
      password,
      contactNo,
      city,
      pin,
      sex,
      age,
    }
    const url = '/register'
    axios
      .post(url, body, header)
      .then((response) => {
        dispatch({
          type: USER_SIGNUP_SUCCESS,
          payload: response.data,
        })
      })
      .catch((error) => {
        dispatch({
          type: USER_SIGNUP_FAIL,
          payload: error,
        })
      })
  }
}

