export const UserUpdateProfile = (email, city, pin, age, contactNo, id) => {
  return (dispatch) => {
    dispatch({
      type: USER_UPDATEPROFILE_REQUEST,
    })
    const header = {
      headers: {
        'Content-Type': 'application/json',
        token: sessionStorage['token'], ///IMP
      },
    }
    const body = {
      email,
      city,
      pin,
      age,
      contactNo,
    }
    const url = '/update/' + id
    axios
      .put(url, body, header)
      .then((response) => {
        dispatch({
          type: USER_UPDATEPROFILE_SUCCESS,
          payload: response.data,
        })
      })
      .catch((error) => {
        dispatch({
          type: USER_UPDATEPROFILE_FAIL,
          payload: error,
        })
      })
  }
}

export const BookTickets = (
  busId,
  userId,
  ownerName,
  phoneNo,
  fare,
  noOfSeats,
  bookDate
) => {
  return (dispatch) => {
    dispatch({
      type: USER_BOOKTICKET_REQUEST,
    })
    const header = {
      headers: {
        'Content-Type': 'application/json',
      },
    }
    const body = {
      ownerName,
      phoneNo,
      fare,
      noOfSeats,
      bookDate,
    }
    const url =
      '/customer/bookTicket/' + userId + '/' + busId
    axios
      .post(url, body, header)
      .then((response) => {
        dispatch({
          type: USER_BOOKTICKET_SUCCESS,
          payload: response.data,
        })
      })
      .catch((error) => {
        dispatch({
          type: USER_BOOKTICKET_FAIL,
          payload: error,
        })
      })
  }
}

