import 'react-app-polyfill/stable'
import './App.css'
import {
  BrowserRouter as Router,
  Redirect,
  Route,
  Switch,
} from 'react-router-dom'
import HomeScreen from './screens/HomeScreen'
import HomeScreenAdmin from './screens/HomeScreenAdmin'
import Navigation from './components/Navigation'
import NavigationAdmin from './components/NavigationAdmin'
import RegistrationScreen from './screens/RegistrationScreen'
import LoginScreen from './screens/LoginScreen'
import updateProfile from './screens/updateProfile'
import ChangePassword from './screens/ChangePassword'
import AddRoutesScreen from './screens/AddRoutesScreen'
import BusListScreen from './screens/BusListScreen'
import RoutesList from './screens/RoutesList'
import AdminChangePassword from './screens/AdminChangePassword'
import UserChangePassword from './screens/UserChangePassword'
import UpdateProfile from './screens/updateProfile'
import UpdateProfileUsers from './screens/UserupdateProfile'
import AddBus from './screens/AddBus'
import SearchList from './screens/SearchList'
import BookingScreen from './screens/BookingScreen'
import BookingList from './screens/BookingList'
import MyBookingScreen from './screens/MyBookingScreen'
import NormalNavigation from './components/NormalNavigation'
import NormalHomeScreen from './screens/NormalHomeScreen'
function App() {
  return (
    <Router>
      <div>
        <NormalNavigation />
        <NavigationAdmin />
        <Navigation />
        <div className="container">
          <Switch>
            <Route
              path="/userchangepassword"
              component={UserChangePassword}
            />
            <Route
              exact
              path="/adminchangepassword"
              component={AdminChangePassword}
            />
            <Route  path="/changepassword" component={ChangePassword} />
            <Route  path="/home" component={HomeScreen} />
            <Route  path="/homeadmin" component={HomeScreenAdmin} />
            <Route  path="/signIn" component={LoginScreen} />
            <Route path="/register" component={RegistrationScreen} />
            <Route  path="/updateProfile" component={UpdateProfile} />
            <Route  path="/addRoutes" component={AddRoutesScreen} />
            <Route  path="/busList" component={BusListScreen} />
            <Route  path="/routesList" component={RoutesList} />
            <Route path="/bookinglist" component={BookingList} />
            <Route path="/mybooking" component={MyBookingScreen} />
            <Route extac path="/" component={NormalHomeScreen} />
            <Route
              exact
              path="/userupdateprofile"
              component={UpdateProfileUsers}
            />
            <Route exact path="/addbus" component={AddBus} />
            <Route exact path="/searchList" component={SearchList} />
            <Route exact path="/booking" component={BookingScreen} />
          </Switch>
        </div>
      </div>
    </Router>
  )
}
export default App
