import { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { getBuses } from '../actions/adminAction'
import Header from '../components/Header'

const BusListScreen = (props) => {
  const dispatch = useDispatch()
  const adminBusList = useSelector((store) => store.adminBusList)
  const { error, response, loading } = adminBusList

  useEffect(() => {
    dispatch(getBuses())
  }, [])
  useEffect(() => {}, [error, response, loading])
  return (
    <div>
      <table class="table table-dark table-striped">
        <thead>
          <tr>
            <th scope="col">Id</th>
            <th scope="col">BusNo</th>
            <th scope="col">Capacity</th>
            <th scope="col">BusType</th>
            <th scope="col">ArrivalDate</th>
            <th scope="col">DestinationDate</th>
            <th scope="col">Price</th>
          </tr>
        </thead>
        <tbody>
          {response &&
            response &&
            response.length > 0 &&
            response.map((adminBusList) => {
              return (
                <tr>
                  <td>{adminBusList.id}</td>
                  <td>{adminBusList.busNo}</td>
                  <td>{adminBusList.capacity}</td>
                  <td>{adminBusList.type}</td>
                  <td>{adminBusList.arrivalDate}</td>
                  <td>{adminBusList.destinationDate}</td>
                  <td>{adminBusList.price}</td>
                </tr>
              )
            })}
        </tbody>
      </table>
    </div>
  )
}

export default BusListScreen
