import {
  ADMIN_ADDROUTE_REQUEST,
  ADMIN_ADDROUTE_SUCCESS,
  ADMIN_ROUTE_FAIL,
  BUSLIST_FETCH_FAIL,
  BUSLIST_FETCH_REQUEST,
  BUSLIST_FETCH_RESET,
  BUSLIST_FETCH_SUCCESS,
  ROUTESLIST_FETCH_REQUEST,
  ROUTESLIST_FETCH_FAIL,
  ROUTESLIST_FETCH_RESET,
  ROUTESLIST_FETCH_SUCCESS,
  BUSADD_FETCH_REQUEST,
  BUSADD_FETCH_SUCCESS,
  BUSADD_FETCH_FAIL,
  USERLISTBYBUS_FETCH_REQUEST,
  USERLISTBYBUS_FETCH_SUCCESS,
  USERLISTBYBUS_FETCH_FAIL,
  ADMIN_REMOVESEAT_REQUEST,
  ADMIN_REMOVESEAT_FAIL,
  ADMIN_REMOVESEAT_SUCCESS,
  ADMIN_ADDSEAT_REQUEST,
  ADMIN_ADDSEAT_SUCCESS,
  ADMIN_ADDSEAT_FAIL,
} from '../constants/adminConstants.js'

export const adminAddRoutesReducer = (state = {}, action) => {
  switch (action.type) {
    case ADMIN_ADDROUTE_REQUEST:
      return { loading: true }
    case ADMIN_ADDROUTE_SUCCESS:
      return { loading: false, response: action.payload, isSuccess: true }
    case ADMIN_ROUTE_FAIL:
      return { loading: false, error: action.payload }
    default:
      return state
  }
}

export const fetchBusesReducer = (state = {}, action) => {
  switch (action.type) {
    case BUSLIST_FETCH_REQUEST:
      return { loading: true }
    case BUSLIST_FETCH_SUCCESS:
      return { loading: false, response: action.payload }
    case BUSLIST_FETCH_FAIL:
      return { loading: false, error: action.payload }
    case BUSLIST_FETCH_RESET:
      return {}
    default:
      return state
  }
}

